@charset "UTF-8";
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Black.woff2") format("woff2"), url("../fonts/Montserrat-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
}

img {
  max-width: 100%;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.secondary-title {
  font-weight: 900;
  font-size: 20px;
  line-height: 1.5;
  text-align: center;
}
@media (min-width: 768px) {
  .secondary-title {
    font-size: 30px;
  }
}
@media (min-width: 1200px) {
  .secondary-title {
    width: 700px;
    margin: 0 auto;
  }
}

.under-title-text {
  text-align: center;
  margin-top: 15px;
  font-size: 16px;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .under-title-text {
    margin-top: 20px;
    font-size: 18px;
  }
}

.container {
  min-width: 280px;
  max-width: 500px;
  padding: 0 20px;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container {
    width: 660px;
    padding: 0;
    max-width: none;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1200px;
  }
}

.button {
  text-transform: uppercase;
  border: none;
  color: #ffffff;
  white-space: nowrap;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
}

.header {
  background-color: #191919;
  color: #ffffff;
  padding: 30px 0;
}

.header__outer-wrapper {
  display: flex;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .header__outer-wrapper {
    display: grid;
    grid-template-columns: min-content 1fr;
  }
}

.header__logo {
  display: flex;
  align-items: center;
  margin: 0 auto;
  transform: translateX(20px);
  width: 170px;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
    transform: none;
  }
}

.header__logo-text {
  margin-left: 15px;
  font-weight: bold;
}

.header__toggle {
  width: 50px;
  height: 50px;
  border: none;
  background-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .header__toggle {
    display: none;
  }
}

.header__burger {
  position: relative;
  display: block;
  font-size: 0;
  width: 40px;
  height: 3px;
  background-color: #fff;
}
.header__burger::before, .header__burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 40px;
  height: 3px;
  background-color: #fff;
}
.header__burger::before {
  top: 10px;
}
.header__burger::after {
  top: -10px;
}

.header__inner-wrapper {
  display: none;
}
.header__inner-wrapper--opened {
  position: absolute;
  z-index: 1;
  display: block;
  background-color: #191919;
  height: 100vh;
  right: 0;
  top: 0;
  padding: 50px;
}
@media (min-width: 768px) {
  .header__inner-wrapper {
    display: grid;
    grid-template: "phones call" "hours call";
    grid-template-columns: 1fr min-content;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .header__inner-wrapper {
    grid-template: "hours phones call";
    grid-template-columns: 1fr min-content min-content;
  }
}

.header__close {
  position: relative;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
}
.header__close::before, .header__close::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 3px;
  background-color: #fff;
}
.header__close::before {
  transform: rotate(45deg);
}
.header__close::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .header__close {
    display: none;
  }
}

.header__working-hours {
  white-space: nowrap;
  text-align: center;
  margin-top: 50px;
  line-height: 1.7;
}
@media (min-width: 768px) {
  .header__working-hours {
    grid-area: hours;
    margin-top: 5px;
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  .header__working-hours {
    margin-left: 150px;
  }
}

@media (min-width: 768px) {
  .header__time br {
    display: none;
  }
}
@media (min-width: 1200px) {
  .header__time br {
    display: block;
  }
}

.header__phones {
  text-align: center;
  margin-top: 30px;
  white-space: nowrap;
}
@media (min-width: 768px) {
  .header__phones {
    grid-area: phones;
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .header__phones {
    margin-right: 25px;
  }
}

.header__phone {
  display: block;
  color: #ffffff;
  text-decoration: underline;
  text-decoration-style: dotted;
  text-decoration-color: rgba(255, 255, 255, 0.3);
  text-underline-offset: 5px;
  font-weight: bold;
  margin-bottom: 20px;
  font-size: 22px;
}
.header__phone:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .header__phone {
    margin-bottom: 10px;
    font-size: 14px;
    display: inline-block;
  }
}
@media (min-width: 1200px) {
  .header__phone {
    display: block;
    font-size: 18px;
  }
}

.header__slash {
  display: none;
}
@media (min-width: 768px) {
  .header__slash {
    display: inline;
    color: #888;
  }
}
@media (min-width: 1200px) {
  .header__slash {
    display: none;
  }
}

.header__request-call {
  width: 100%;
  height: 50px;
  background-color: #1669b3;
  margin-top: 50px;
}
@media (min-width: 768px) {
  .header__request-call {
    grid-area: call;
    margin-top: 0;
    width: 150px;
  }
}

@media (min-width: 768px) {
  .main {
    background-image: url("../img/main-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
  .main {
    background-image: url("../img/main-bg@2x.jpg");
  }
}

.main__container {
  padding: 0;
  max-width: 350px;
}
@media (min-width: 768px) {
  .main__container {
    max-width: none;
  }
}

.main__wrapper {
  background-color: #f7b516;
  padding: 30px;
}
@media (min-width: 768px) {
  .main__wrapper {
    width: 350px;
  }
}
@media (min-width: 1200px) {
  .main__wrapper {
    width: 570px;
    padding: 95px;
  }
}

.main__title {
  font-size: 22px;
  font-weight: 900;
  text-align: center;
  margin: 0;
}
@media (min-width: 768px) {
  .main__title {
    font-size: 26px;
  }
}
@media (min-width: 1200px) {
  .main__title {
    font-size: 38px;
    text-align: left;
    line-height: 1.3;
  }
}

.main__title br, .main__text br {
  display: none;
}
@media (min-width: 1200px) {
  .main__title br, .main__text br {
    display: block;
  }
}

.main__text {
  margin-top: 10px;
  font-size: 14px;
  text-align: center;
  font-weight: bold;
}
@media (min-width: 1200px) {
  .main__text {
    margin-top: 50px;
    font-size: 24px;
    text-align: left;
  }
}

.main__advantages {
  list-style: none;
  margin-top: 30px;
  font-size: 16px;
  line-height: 1.5;
}
@media (min-width: 1200px) {
  .main__advantages {
    margin-top: 80px;
    font-size: 18px;
  }
}

.main__advantage {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  padding-left: 50px;
}
.main__advantage:last-child {
  margin-bottom: 0;
}
.main__advantage::before {
  content: "";
  display: block;
  position: absolute;
  width: 30px;
  height: 30px;
  left: 0;
}
@media (min-width: 1200px) {
  .main__advantage {
    margin-bottom: 45px;
  }
}
.main__advantage--price::before {
  background-image: url("../img/piggy.svg");
}
.main__advantage--delivery::before {
  background-image: url("../img/delivery.svg");
}
.main__advantage--installment::before {
  background-image: url("../img/installment.svg");
}
.main__advantage--guarantee::before {
  background-image: url("../img/guarantee.svg");
}

.get-price {
  padding: 50px 0;
}
@media (min-width: 768px) {
  .get-price {
    padding: 70px 0;
  }
}
@media (min-width: 1200px) {
  .get-price {
    background-image: url("../img/get-price-bg.jpg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center left;
    padding: 100px 0;
  }
}
@media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 1.5dppx) {
  .get-price {
    background-image: url("../img/get-price-bg@2x.jpg");
  }
}

@media (min-width: 768px) {
  .get-price__title {
    font-size: 26px;
  }
}
@media (min-width: 1200px) {
  .get-price__title {
    text-align: right;
  }
}

.get-price__title br {
  display: none;
}
@media (min-width: 1200px) {
  .get-price__title br {
    display: block;
  }
}

.get-price__form {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .get-price__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}
@media (min-width: 1200px) {
  .get-price__form {
    width: 700px;
    margin-left: auto;
    margin-top: 60px;
  }
}

.get-price__input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.get-price__input-wrapper:nth-child(2) {
  margin-bottom: 0;
}
.get-price__input-wrapper::after {
  content: "";
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  right: 20px;
}
.get-price__input-wrapper--email::after {
  background-image: url("../img/envelope.svg");
}
.get-price__input-wrapper--phone::after {
  background-image: url("../img/phone.svg");
}
@media (min-width: 768px) {
  .get-price__input-wrapper {
    margin-bottom: 0;
  }
}

.get-price__input {
  display: block;
  width: 100%;
  padding: 20px;
  padding-right: 70px;
  font-size: 16px;
  background-color: #f2f2f2;
  border: none;
}
@media (min-width: 1200px) {
  .get-price__input {
    padding: 30px;
    padding-right: 70px;
  }
}

.get-price__text {
  margin-top: 30px;
  font-size: 11px;
  text-align: center;
}
@media (min-width: 768px) {
  .get-price__text {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    align-self: center;
    margin-top: 0;
    padding-right: 50px;
    text-align: right;
    font-size: 14px;
    font-weight: bold;
  }
  .get-price__text::after {
    content: "";
    position: absolute;
    display: block;
    width: 30px;
    height: 30px;
    right: 0;
    background-image: url("../img/clock.svg");
  }
}

.get-price__text br {
  display: none;
}
@media (min-width: 768px) {
  .get-price__text br {
    display: block;
  }
}

.get-price__submit {
  margin-top: 10px;
  width: 100%;
  padding: 20px 0;
  background-color: #1669b3;
}
@media (min-width: 768px) {
  .get-price__submit {
    margin-top: 0;
  }
}
@media (min-width: 1200px) {
  .get-price__submit {
    padding: 30px 0;
  }
}

.advantages {
  padding: 30px 0;
  background-color: #08243e;
  color: #ffffff;
}
@media (min-width: 768px) {
  .advantages {
    padding: 70px 0;
  }
}
@media (min-width: 1200px) {
  .advantages {
    padding: 100px 0;
  }
}
.advantages--light {
  background-color: #fff;
  color: #000000;
}

.advantages__title br {
  display: none;
}
@media (min-width: 1200px) {
  .advantages__title br {
    display: block;
  }
}

.advantages__list {
  list-style: none;
  margin-top: 50px;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
    margin-top: 80px;
  }
}
@media (min-width: 1200px) {
  .advantages__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: unset;
  }
}

@media (min-width: 768px) {
  .advantages--light .advantages__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
  }
}
@media (min-width: 1200px) {
  .advantages--light .advantages__list {
    justify-content: space-between;
  }
}

.advantages__item {
  margin-bottom: 100px;
}
.advantages__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .advantages__item {
    display: grid;
    grid-template-rows: 125px 1fr;
    align-items: center;
    margin-bottom: 0;
    width: 300px;
  }
}
@media (min-width: 1200px) {
  .advantages__item {
    width: 270px;
  }
}

.advantages__img {
  display: block;
  margin: 0 auto;
}

.advantages__description {
  text-align: center;
  margin-top: 30px;
  line-height: 1.5;
}

.advantages__dots {
  margin: 0 auto;
  margin-top: 50px;
  display: grid;
  grid-auto-flow: column;
  justify-content: center;
  column-gap: 10px;
}

.advantages__dot {
  background-color: #f2f2f2;
  width: 15px;
  height: 15px;
  border: none;
  border-radius: 50%;
}
.advantages__dot.active {
  background-color: #f7b516;
}

.detail {
  padding: 30px 0;
  background-color: #123050;
  color: #ffffff;
}
@media (min-width: 768px) {
  .detail {
    padding: 70px 0;
  }
}
@media (min-width: 1200px) {
  .detail {
    padding: 100px 0;
    background-image: url("../img/detail-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
}
@media (min-width: 1200px) and (min-resolution: 144dpi), (min-width: 1200px) and (min-resolution: 192dppx) {
  .detail {
    background-image: url("../img/detail-bg@2x.jpg");
  }
}

.detail__text br {
  display: none;
}
@media (min-width: 768px) {
  .detail__text br {
    display: block;
  }
}

.detail__form {
  margin-top: 50px;
}
@media (min-width: 768px) {
  .detail__form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }
}
@media (min-width: 1200px) {
  .detail__form {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.detail__input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.detail__input-wrapper:nth-child(2) {
  margin-bottom: 0;
}
.detail__input-wrapper::after {
  content: "";
  position: absolute;
  display: block;
  width: 20px;
  height: 20px;
  right: 20px;
}
.detail__input-wrapper--phone::after {
  background-image: url("../img/phone.svg");
}
@media (min-width: 768px) {
  .detail__input-wrapper {
    margin-bottom: 0;
  }
}

.detail__input {
  display: block;
  width: 100%;
  padding: 20px;
  padding-right: 70px;
  font-size: 16px;
  background-color: #f2f2f2;
  border: none;
}
@media (min-width: 1200px) {
  .detail__input {
    padding: 30px;
    padding-right: 70px;
  }
}

.detail__submit {
  width: 100%;
  padding: 20px 0;
  color: #ffffff;
  background-color: #f7b516;
  margin-top: 40px;
}
@media (min-width: 768px) {
  .detail__submit {
    margin-top: 0;
    grid-area: 2/1/3/3;
  }
}
@media (min-width: 1200px) {
  .detail__submit {
    grid-area: unset;
  }
}

.detail__postscript {
  text-align: center;
  line-height: 1.5;
  margin-top: 50px;
  font-size: 14px;
}

.detail__call-text {
  text-align: center;
  line-height: 1.5;
  margin-top: 30px;
  font-size: 14px;
}

.detail__call {
  display: block;
  color: #ffffff;
  text-align: center;
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-style: dotted;
  text-decoration-color: #888;
  margin-top: 20px;
  font-size: 28px;
  font-weight: bold;
}

.guarantee {
  padding: 30px 0;
}
@media (min-width: 768px) {
  .guarantee {
    padding: 70px 0;
  }
}
@media (min-width: 1200px) {
  .guarantee {
    padding: 100px 0;
  }
}

.guarantee__list {
  list-style: none;
  margin-top: 50px;
}
@media (min-width: 768px) {
  .guarantee__list {
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.guarantee__item {
  margin-bottom: 70px;
}
.guarantee__item:last-child {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .guarantee__item {
    margin-bottom: 0;
    width: 200px;
  }
}
@media (min-width: 1200px) {
  .guarantee__item {
    width: auto;
  }
}

.guarantee__img {
  display: block;
  margin: 0 auto;
  object-fit: cover;
