}

.guarantee__text {
  text-align: center;
  margin-top: 35px;
}
@media (min-width: 768px) {
  .guarantee__text {
    font-size: 16px;
  }
}
@media (min-width: 1200px) {
  .guarantee__text {
    font-size: 18px;
  }
}

.guarantee__more {
  color: #000000;
  display: block;
  text-align: center;
  margin-top: 30px;
  font-weight: bold;
}

.faq {
  padding: 30px 0;
  background-color: #123050;
  color: #ffffff;
}
@media (min-width: 768px) {
  .faq {
    padding: 70px 0;
  }
}
@media (min-width: 1200px) {
  .faq {
    padding: 100px 0;
  }
}

.faq__inner-wrapper {
  margin-top: 50px;
}
@media (min-width: 1200px) {
  .faq__inner-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    margin-top: 80px;
    align-items: start;
  }
}

.faq__list {
  margin-left: 20px;
}

.faq__item {
  position: relative;
  margin-bottom: 20px;
  padding-left: 20px;
}
.faq__item:last-child {
  margin-bottom: 0;
}
.faq__item--hidden {
  display: none;
}
@media (min-width: 768px) {
  .faq__item {
    margin-bottom: 50px;
  }
}

.faq__question {
  background-color: transparent;
  border: none;
  color: #ffffff;
  text-align: left;
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-color: rgba(255, 255, 255, 0.5);
  font-size: 16px;
  line-height: 1.5;
  cursor: pointer;
}

.faq__answer {
  display: none;
}
.faq__answer--opened {
  position: absolute;
  display: block;
  background-color: #fff;
  color: #000000;
  line-height: 1.5;
  padding: 20px;
  padding-right: 30px;
  bottom: 0;
  left: -30px;
  right: -10px;
  z-index: 1;
  transform: translateY(calc(100% + 10px));
}
.faq__answer--opened::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 15px;
  background-color: #fff;
